.my-plan-card {
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style-type: none;
  counter-reset: my-plan-counter;
}

.my-plan-card__item {
  counter-increment: my-plan-counter;
  background-color: @fg-main;
  border-radius: 10px;
  padding: 24px 25px 23px;
  position: relative;

  &:last-child .my-plan-card__description--dates {
    margin-left: -1px;
  }

  @media (min-width: @tablet-width) {
    padding: 40px 40px 14px;
    display: grid;
    grid-template-columns: 177px 1fr 108px;
    grid-row-gap: 54px;
    grid-template-areas:
      "plan-header plan-header plan-header"
      "plan-ntravellers plan-dates plan-change";

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      top: 97px;
      left: 0;
      background-color: @fg-border;
    }

    &:last-child .my-plan-card__description--dates {
      margin-left: 4px;
    }
  }

  @media (min-width: @desktop-width) {
    padding: 38px 40px 37px;
    display: grid;
    grid-template-columns: 1fr 177px 304px 109px;
    grid-row-gap: 0;
    grid-template-areas: "plan-header plan-ntravellers plan-dates plan-change";

    &::before {
      content: none;
    }

    &:last-child .my-plan-card__description--dates {
      margin-left: -3px;
    }
  }
}

.my-plan-card__item + .my-plan-card__item {
  margin-top: 10px;

  @media (min-width: @tablet-width) {
    margin-top: 10px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 15px;
  }
}

.my-plan-card__title {
  margin: 0 0 20px;
  .common-font(16px, 16px, 700);
  grid-area: plan-header;
  position: relative;

  &::before {
    content: counter(my-plan-counter) + ". ";
    font-family: "Blogger Sans", "Arial", sans-serif;
    color: @fg-blue-light;
    .common-font(16px, 16px, 500);
  }

  @media (min-width: @tablet-width) {
    .common-font(24px, 24px, 700);
    margin-bottom: 0;

    &::before {
      .common-font(24px, 24px, 700);
      position: absolute;
      right: -10px;
      top: 2px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    margin-top: -2px;

    &::before {
      position: relative;
      margin-right: 33px;
      right: -3px;
      top: -1px;

      .common-font(20px, 20px, 500);
    }
  }
}

.my-plan-card__description {
  .common-font(14px, 14px, 500);
  letter-spacing: 0.06em;
  text-transform: uppercase;
  margin-top: 0;

  &--ntravellers {
    grid-area: plan-ntravellers;
    margin-bottom: 5px;
  }

  &--dates {
    grid-area: plan-dates;
    margin-bottom: 24px;
  }

  @media (min-width: @tablet-width) {
    .common-font(20px, 20px, 500);
    letter-spacing: 0.1em;
    margin-top: 0;


    &--ntravellers {
      margin-bottom: 0;
    }

    &--dates {
      margin-bottom: 0;
      letter-spacing: 0.06em;
    }
  }
}

.my-plan-card__button {
  .common-font(14px, 14px, 500);
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: fade(@fg-black, 30%);
  grid-area: plan-change;
  .transition(color, outline;);

  &:hover {
    color: fade(@fg-black, 60%);
  }

  &:active {
    color: fade(@fg-black, 10%);
  }

  &:focus {
    outline-color: @bg-secondary;
    border-radius: 4px;
  }

  @media (min-width: @tablet-width) {
    .common-font(20px, 20px, 500);
    letter-spacing: 0.1em;
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    letter-spacing: 0.08em;
  }
}
