.button {
  display: block;
  text-decoration: none;
  cursor: pointer;
  border: none;
  font-family: "Blogger Sans", "Arial", sans-serif;
  transition-property: outline, color, background-color;
  transition-duration: 0.3s;
}
.button:focus {
  outline: 1px solid #FFD74B;
  outline-offset: 4px;
}
.button--primary {
  background-color: #FFD74B;
}
.button--primary:hover {
  background-color: #FFBD30;
}
.button--primary:active {
  color: rgba(22, 28, 53, 0.3);
}
.button--secondary {
  background-color: #FF8D30;
}
.button--form-round {
  border-radius: 50%;
}
.button--form-oval {
  border-radius: 25px;
}
.button--form-rectangle {
  border-radius: 10px;
}
@media (min-width: 768px) {
  .button--form-rectangle {
    border-radius: 20px;
  }
}
.button--with-icon {
  display: flex;
}
.button__icon-right {
  display: block;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: auto;
}
.button__icon-left {
  display: block;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  transform: rotate(180deg);
}
.button__text {
  display: block;
}
