.checkbox-control {
  display: flex;
  gap: 9px;
  align-items: center;
  outline-color: transparent;
  transition-property: opacity, outline-color;
  transition-duration: 0.3s;
}
.checkbox-control:focus-within {
  outline: 1px solid #161C35;
  border-radius: 4px;
  outline-offset: 2px;
}
.checkbox-control:hover {
  opacity: 0.7;
}
