  opacity: 0.3;
}
@media (min-width: 768px) {
  .checkbox-control {
    gap: 12px;
  }
}
.checkbox-control__input + .checkbox-control__mark::before {
  border-color: transparent;
}
.checkbox-control__input:checked + .checkbox-control__mark::before {
  border-left: 1px solid #161C35;
  border-bottom: 1px solid #161C35;
}
.checkbox-control__mark {
  position: relative;
  width: 18px;
  height: 18px;
  top: -2px;
  background-color: #FFFFFF;
  border-radius: 4px;
}
.checkbox-control__mark::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 4px;
  top: 5px;
  left: 5px;
  border: 1px solid transparent;
  transform: rotate(-45deg);
  transition-property: border-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .checkbox-control__mark {
    width: 24px;
    height: 24px;
    top: -1px;
  }
  .checkbox-control__mark::before {
    top: 7px;
    left: 7px;
  }
}
.checkbox-control__label {
