.tooltip {
  position: relative;
}
.tooltip:hover {
  z-index: 10;
}
.tooltip:hover::after {
  content: "";
  position: absolute;
  width: 7px;
  height: 7px;
  top: calc(100% + 5px);
  left: 50%;
  transform: rotate(45deg) translateX(-50%);
  background-color: #101D41;
}
.tooltip:hover::before {
  content: attr(data-tooltip);
  position: absolute;
  width: max-content;
  padding: 18px 25px;
  border-radius: 4px;
  top: calc(100% + 1px + 5px);
  left: 50%;
  transform: translateX(-50%);
  color: #FFFFFF;
  background-color: #101D41;
}
.tooltip--top:hover::after {
  top: calc(100% + -30px);
}
.tooltip--top:hover::before {
  top: calc(-100% - 10px + -30px);
}
@media (min-width: 768px) {
  .tooltip--top:hover::after {
    top: calc(100% - 1px + -30px);
  }
}
.tooltip--personal:hover::after {
  top: calc(100% + -40px);
}
.tooltip--personal:hover::before {
  top: calc(-100% - 10px + -40px);
}
@media (min-width: 768px) {
  .tooltip--personal:hover::after {
    top: calc(100% - 1px + -40px);
  }
}
