.checkbox-control {
  display: flex;
  gap: 9px;
  align-items: center;

  outline-color: transparent;
  .transition(opacity, outline-color;);

  &:focus-within {
    outline: 1px solid @bg-main;
    border-radius: 4px;
    outline-offset: 2px;
  }

  &:hover {
    .hover();
  }

  &:active {
    .active();
  }

  @media (min-width: @tablet-width) {
    gap: 12px;
  }
}

.checkbox-control__input {
  & + .checkbox-control__mark::before {
    border-color: transparent;
  }

  &:checked + .checkbox-control__mark::before {
    border-left: 1px solid @bg-main;
    border-bottom: 1px solid @bg-main;
  }
}

.checkbox-control__mark {
  position: relative;
  width: 18px;
  height: 18px;
  top: -2px;

  background-color: @fg-main;
  border-radius: 4px;

  &::before {
    content: "";
    position: absolute;
    width: 8px;
    height: 4px;
    top: 5px;
    left: 5px;
    border: 1px solid transparent;
    transform: rotate(-45deg);
    .transition(border-color);
  }

  @media (min-width: @tablet-width) {
    width: 24px;
    height: 24px;
    top: -1px;

    &::before {
      top: 7px;
      left: 7px;
    }
  }
}

.checkbox-control__label {
  .common-font(15px, 20px, 400);

  @media (min-width: @tablet-width) {
    font-size: 20px;
  }
}
